import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App";

export function initializeApp(container) {
  if (!container) {
    console.error("Le conteneur passé à initializeApp est invalide.");
    return;
  }

  console.log("Container reçu par initializeApp :", container);

  // Ajout d'un paramètre `ownerDocument` pour prendre en compte le ShadowRoot
  const root = ReactDOM.createRoot(container, {
    hydrate: false,
    // Forcer React à comprendre que le `document` du Shadow DOM doit être utilisé
    document: container.ownerDocument,
  });

  root.render(
    <React.StrictMode>
      <App />
    </React.StrictMode>
  );
}

// Attacher la fonction globale à `window`
window.initializeApp = initializeApp;